.user-details-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 10px 0;
  background: rgb(24, 24, 24);
}

.user-image {
border-radius: 50%;
height: 30px;
background: -webkit-gradient(linear,left top, right top,from(#c9373f),to(#930f5b));
background: -webkit-linear-gradient(left,#c9373f,#930f5b);
background: -o-linear-gradient(left,#c9373f,#930f5b);
background: linear-gradient(90deg,#c9373f,#930f5b);
width: 30px;
}

.user-name {
  color: #fff;
  font-size: 14px;
  position: relative;
  top: 5px;
  margin-left: 10px;
}



