.browse-headers {
  display: -ms-flexbox;
  display: flex;
}

.browse-headers p {
  color: rgb(179, 179, 179);
  font-size: 14px;
  text-transform: uppercase;
  font-family: 'Proxima Thin', Georgia, sans-serif;
  letter-spacing: 1px;
  margin: 0 20px;
  cursor: pointer;
}

.browse-headers p:first-child {
  margin: 0 20px 0 0;
}

.browse-headers p:hover {
  color: #fff;
}

.playlist-image {
  width: 230px;
}

.playlist-title-container {
  display: -ms-flexbox;
  display: flex;
}

.playlist-info-container {
