.header-title {
  font-size: 48px;
  padding: 13px 0 25px;
}

.browse-headers .active {
  position: relative;
}

.browse-headers .active:after {
  content: "";
  position: absolute;
  width: 35px;
  left: 50%;
  top: 22px;
  border-bottom: 2px solid #930f5b;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.current-artist-header-container {
	display: -ms-flexbox;
	display: flex;
}

.current-artist-image {
	width: 180px;
	height: 180px;
	border-radius: 50%;
