.current-artist-info {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	padding-left: 20px;
}

.current-artist-info p {
	text-transform: uppercase;
	font-family: 'Proxima Thin', Georgia, sans-serif;
	font-size: 13px;
}

.artist-button {
	margin-top: 20px;
	margin-left: 40px;
}



