.volume-container {
  position: absolute;
  top: 48px;
	right: 10px;
}

.volume {
  -webkit-appearance: none;
  width: 120px;
  height: 5px;
  background: -webkit-gradient(linear,left top, right top,from(#c9373f),to(#930f5b));
  background: -webkit-linear-gradient(left,#c9373f,#930f5b);
  background: -o-linear-gradient(left,#c9373f,#930f5b);
  background: linear-gradient(90deg,#c9373f,#930f5b);
  border-radius: 4px;
  outline: none;
  opacity: 1;
}

input[type=range]::-moz-range-track {
  width: 120px;
  height: 5px;
  background: linear-gradient(90deg,#c9373f,#930f5b);
  border-radius: 4px;
  outline: none;
  opacity: 1;
}

.volume-container  .fa-volume-up {
	position: relative;
	top: 3px;
	margin-right: 5px;
}

.volume:hover {
    opacity: 0.8;
    background: -webkit-gradient(linear,left top, right top,from(#c9373f),to(#930f5b));
    background: -webkit-linear-gradient(left,#c9373f,#930f5b);
    background: -o-linear-gradient(left,#c9373f,#930f5b);
    background: linear-gradient(90deg,#c9373f,#930f5b);
}

.volume::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 12px;
  height: 12px;
  background: #fff;
  cursor: pointer;
  border-radius: 50%;
}



